body {
  font-family: "Sen", sans-serif;
}

.head {
  display: flex;
  font-size: 17px;
  letter-spacing: 1px;
  margin-top: 40px;
  justify-content: center;
  align-items: center;
}

.head_icon {
  width: 35px;
  height: 35px;
  margin: 0px 10px;
}

.head h1 {
  color: white;
}

.precautions p {
  color: white;
  width: 500px;
  font-size: 24px;
  display: flex;
  padding: 5px;
  /* text-align: center; */
  /* border: 1px solid whitesmoke; */
  /* cursor: pointer; */
  transition: 0.3s;
}

.icon {
  width: 29px;
  height: 29px;
  margin: 0px 10px;
  color: #faed26;
}

p:hover {
  transform: scale(1.07);
}
