body {
  background-color: #19181a;
  color: #faed26;
}

.c_data {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
  margin-bottom: -20px;
}

.c_head {
  letter-spacing: 1px;
  font-family: "Sen", sans-serif;
  color: white;
  padding: 10px;
  text-align: center;
  font-size: 26px;
}

.c_headPNG {
  width: 30px;
  height: 30px;
  margin-right: -5px;
  margin-top: 4px;
}

.worldData {
  display: flex;
  color: white;
  background-color: rgb(37, 35, 35);
  margin-top: 30px;
  padding: 10px;
  box-shadow: 0px 0px 1px #faed26, 0px 0px 20px black, 0px 0px 20px black,
    0px 0px 20px black;
  cursor: pointer;
  transition: 0.3s;
}

.worldData:hover {
  transform: scale(1.02);
}

.worldIcon {
  color: #faed26;
  margin-right: -90px;
  margin-top: 4px;
}

.data {
  font-family: "Sen", sans-serif;
  padding: 5px;
  margin: auto;
  font-size: 21px;
  margin-left: 90px;
  font-size: 20px;
}

.wRecovered {
  font-family: "Rajdhani", sans-serif;
  color: rgb(0, 255, 0);
  letter-spacing: 1px;
}

.wDeaths {
  color: rgb(255, 0, 0);
  font-family: "Rajdhani", sans-serif;
  letter-spacing: 1px;
}

.wActive {
  font-family: "Rajdhani", sans-serif;
  color: rgb(0, 255, 213);
  letter-spacing: 1px;
}

.countryInfo {
  box-shadow: 0px 0px 1px #faed26, 0px 0px 30px black, 0px 0px 30px black,
    0px 0px 30px black;
  display: flex;
  background-color: rgb(37, 35, 35);
  margin: 20px;
  cursor: pointer;
  transition: 0.2s;
}

.countryInfo:hover {
  transform: scale(1.05);
}

.fan {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 9px;
  height: auto;
  margin-right: 10px;
  font-family: "Sen", sans-serif;
  color: white;
  font-size: 17px;
  letter-spacing: 1px;
}

.fan img {
  width: 105px;
  height: 60px;
}

.info {
  display: flex;
  margin: auto;
}

.info h4 {
  margin: 0px 70px;
  font-family: "Rajdhani", sans-serif;
  font-size: 21px;
  letter-spacing: 1px;
}

.c_deaths {
  color: rgb(255, 0, 0);
}

.c_active {
  color: rgb(0, 255, 213);
}

.c_recovered {
  color: rgb(0, 255, 0);
}

.more_button {
  text-decoration: underline;
  cursor: pointer;
  font-family: "Sen", sans-serif;
  border: none;
  color: #faed26;
  font-size: 20px;
  background: none;
  transition: 0.2s;
  letter-spacing: 1px;
}

.more_button:hover {
  transform: scale(1.2);
  color: white;
}

.more_button:active {
  font-size: 15px;
  color: #faed26;
}

.modal {
  font-family: "Rajdhani", sans-serif;
  letter-spacing: 1px;
}

.modal_country {
  text-decoration: underline;
  font-family: "Sen", sans-serif;
  color: #19181a;
}

.modal_recovered {
  color: rgb(0, 255, 0);
}

.modal_deaths {
  color: rgb(255, 0, 0);
}

.modal_cases {
  color: rgb(0, 98, 69);
}

.modal_critical {
  color: rgb(107, 0, 0);
}
