.navbar {
  display: flex;
  align-items: center;
  padding: 5px;
}

.mainHead {
  font-size: 25px;
  padding: 5px;
  font-family: "Cinzel", serif;
  letter-spacing: 2px;
}

.covidPNG {
  width: 60px;
  height: 60px;
  animation: rotation 10s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(359deg);
  }
}

.list {
  font-family: "Sen", sans-serif;
  display: flex;
  margin: 0px 150px;
  padding: 15px;
}

.listItem {
  cursor: pointer;
  padding: 5px;
  margin: 0px 35px;
  transition: 0.3s;
}

.listItem:hover {
  transform: scale(1.1);
  letter-spacing: 0.5px;
}

.link {
  color: white;
  text-decoration: none;
  font-size: 19px;
}

.link:hover {
  color: #faed26;
}

.link:active {
  font-size: 17px;
  color: white;
}

ul {
  list-style: none;
}

.navIcon {
  margin-right: -33px;
  margin-top: 5px;
  width: 20px;
  height: 20px;
}
